<template>
  <div>
    <index-navbar />
    <section
      class="header relative pt-16 items-center flex h-screen max-h-860-px"
    >
      <div class="container mx-auto items-center flex flex-wrap">
        <div class="w-full md:w-8/12 lg:w-6/12 xl:w-6/12 px-4">
          <div class="pt-32 sm:pt-0">
            <h2 class="font-semibold text-4xl text-blueGray-600">
              {{ data?.config?.head_text }}
            </h2>
            <p class="mt-4 text-lg leading-relaxed text-blueGray-500">
              {{ data?.config?.head_description }}
            </p>
          </div>
        </div>
      </div>

      <img
        class="absolute top-0 b-auto right-0 pt-16 sm:w-6/12 -mt-48 sm:mt-0 w-10/12 max-h-860-px"
        style="object-fit: contain"
        :src="patternReact"
        alt="..."
      />
    </section>

    <NewsComponent />
    <footer-component />
  </div>
</template>
<script>
import IndexNavbar from "@/components/Navbars/IndexNavbar.vue";
import FooterComponent from "@/components/Footers/Footer.vue";
import NewsComponent from "../components/Cards/NewsComponent.vue";

import patternReact from "@/assets/img/pattern_react.png";
import componentBtn from "@/assets/img/component-btn.png";
import componentProfileCard from "@/assets/img/component-profile-card.png";
import componentInfoCard from "@/assets/img/component-info-card.png";
import componentInfo2 from "@/assets/img/component-info-2.png";
import componentMenu from "@/assets/img/component-menu.png";
import componentBtnPink from "@/assets/img/component-btn-pink.png";
import documentation from "@/assets/img/documentation.png";
import login from "@/assets/img/login.jpg";
import profile from "@/assets/img/profile.jpg";
import landing from "@/assets/img/landing.jpg";

export default {
  data() {
    return {
      patternReact,
      componentBtn,
      componentProfileCard,
      componentInfoCard,
      componentInfo2,
      componentMenu,
      componentBtnPink,
      documentation,
      login,
      profile,
      landing,
      data: {},
      news: [],
    };
  },
  components: {
    IndexNavbar,
    FooterComponent,
    NewsComponent,
  },
  setup() {},
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      /** Get Data API */
      this.axios
        .get("https://kemahasiswaan.demo-web.my.id/api/custom/landing/configs")
        .then((response) => {
          /** Save data response */
          this.data = response.data.data;
          this.news = this.data.news;
        });
    },
  },
};
</script>
